.selector {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  padding: 10px;
}

.disclaimer {
  text-align: center;
  font-size: 10px;
  padding: 5px;
}

/* Label color switcher */
.countyLabelLight {
  font-size: 14px;
  color: #242931;
  padding: 10px;
}

.countyLabelDark {
  font-size: 14px;
  color: #fff;
  padding: 10px;
}

/* Country selector color switcher */
.selectorView {
  appearance: none;
  padding: 0.6em 1.4em 0.5em 0.8em;
  border: none;
  border-radius: 10px;
  background-color: #fff;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),
    linear-gradient(to bottom, #ffffff 0%, #fff 100%);
  background-repeat: no-repeat, repeat;
  background-position: right 10px top 50%, 0 0;
  background-size: 8px auto, 100%;
  font-size: 14px;
  color: #242931;
}

.selectorView.selectorViewDark {
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),
    linear-gradient(to bottom, #495466 0%, #495466 100%);
  color: #fff;
}

/* Notification */
.notificationLight, .notificationDark {
  display: none;
}
@media (max-width: 560px) and (min-width: 320px) and (orientation: portrait) {
  .notificationLight {
    display: block;
    padding: 15px;
    text-align: center;
    font-size: 14px;
    color: #fff;
    background-color: #168be8;
    border-radius: 10px;
    margin-top: 20px;
  }
  .notificationDark {
    display: block;
    padding: 15px;
    text-align: center;
    font-size: 14px;
    color: #fff;
    background-color: #495466;
    border-radius: 10px;
    margin-top: 20px;
  }
}

@media (max-width: 370px){
  .selectorView{
    width: 300px;
  }
}